import kotlinx.browser.document
import kotlinx.browser.window
import kotlinx.html.div
import kotlinx.html.dom.append
import org.w3c.dom.Node
import kotlinx.browser.document
import kotlinx.browser.window
import kotlinx.html.DIV
import kotlinx.html.button
import org.w3c.dom.HTMLElement

fun main() {
    val bgcolor = window.location.href.split("?")[1].split("=")[1]
    window.onload = {
        document.body?.sayHello()

        bg(bgcolor)
    }

}

fun bg(color: String) {
    if(color!="") {
        document.bgColor = color
    }
}

fun Node.sayHello() {
    document.bgColor = "110011"
    window.innerWidth


    var d: HTMLElement? = null
    append {
        d = div {
            +window.location.href
        }.also {
            it.style.color = "#FFBBFF"

        }
    }

    append { button {+"aaa"

    }.also { h->
        h.onclick = {
            h.innerHTML = "aaaaaaa"
            Unit
        }
    }

    }



    window.onresize = {
        d!!.innerText = "innerWidth ${window.innerWidth} height ${window.innerHeight} outerWidth ${window.outerWidth} height ${window.outerWidth}"
        Unit
    }
}